myApp.directive('convertEmoji', function() {
  return {
    restrict: 'A',
    link: function (scope, element) {
        var emojiRef = element.text();
        console.log('emoji element: ', element.text());
        var emoji = twemoji.convert.fromCodePoint(emojiRef);

        element.text(emoji);
        
        twemoji.parse(element[0]);
    }
  };
});