myApp.directive('trChatBubble', [ '$timeout', function($timeout) {
  return {
    restrict: 'E',
    templateUrl: 'templates/tr-chat-bubble.html',
    link: function(scope, element) {
      scope.messagePosition = function(message) {
        if (message.align === 'right') {
          return message.align;
        } else {
          return 'left';
        }
      }

      $timeout(function() {
            element.addClass('animating');
        }, 350);
    }
  };
}]);
