myApp.directive('trSimpleChat', ['$timeout', '$interval', 'MockMessagesService', function($timeout, $interval, mockMessages) {
  return {
    restrict: 'E',
    templateUrl: 'templates/tr-simple-chat.html',
    link: function(scope) {
    	scope.messagesData = mockMessages.getMessages();
		
		console.log('scope.messagesData', scope.messagesData);
		scope.conversation = [];
		scope.questions = ['hey'];
		scope.messagesArray = [0,1];
		scope.typing = false;

    	// Get all the messages given an array of indexes
    	scope.getMessages = function(indexes) {
			var messageSet = [];

    		for (var i = 0; i < indexes.length; i++) {
    			messageSet.push(scope.messagesData[indexes[i]]);

				// If this is the last message get the questions
    			if (i === indexes.length - 1) {
					console.log('scope.messagesData[indexes[i]].questions', scope.messagesData[indexes[i]].questions);
    				scope.questions = scope.messagesData[indexes[i]].questions;
    			}
    		}

			return messageSet;
    	}

		scope.pushToConversation = function(messages) {
			var index = 0;

			function addMessage() {
				scope.typing = true;
				$timeout(function() {
					scope.conversation.push(messages[index]);
					index = (index + 1) % messages.length;
					$("html, body").animate({ scrollTop: $(document).height()}, 400);
					scope.typing = false;
				}, 1500);			
			}

			$interval(addMessage, 1500, messages.length);
		}

		scope.pushToConversation(scope.getMessages(scope.messagesArray));
			
    	scope.clickAnswer = function(question) {
			// push this answer to the conversation
			var selectedQuestion = {
				text: question.text,
				align: 'right'
			}
			scope.conversation.push(selectedQuestion);
			
			// go get the next set of answers
			var msgArray = [];

			for (var i = 0; i < question.showNext.length; i++) {
				msgArray.push(question.showNext[i]);
			}

			scope.pushToConversation(scope.getMessages(msgArray));
    	}

    }
  };
}]);